import forge from 'node-forge';

const uuid = localStorage.getItem('uuid');
const publicKey = localStorage.getItem('publicKey');

function encryptRSA(publicKey, data) {
    const publicKeyForge = forge.pki.publicKeyFromPem(publicKey);

    const encryptedData = publicKeyForge.encrypt(data, 'RSA-OAEP', {
        md: forge.md.sha256.create(),
        mgf1: {
            md: forge.md.sha256.create()
        }
    });

    return forge.util.encode64(encryptedData);
}

export const encryptedUuid = (timestamp) => {
    if (uuid && publicKey && publicKey.includes('-----BEGIN PUBLIC KEY-----')) return encryptRSA(publicKey, `${uuid}@${timestamp.getTime()}`);
    else return false;
}